@import url(https://fonts.googleapis.com/css2?family=Heebo:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app{font-family:'Heebo', sans-serif;font-size:18px;text-align:center}

.c-welcome__form{position:relative}.c-welcome__error{display:block;position:absolute;width:100%;font-size:.8rem;color:#F34237;top:0}@media (min-width: 768px){.c-welcome__error{top:-1.5rem}}.c-welcome__input{border:1px solid #a5a5a5;border-radius:.3rem;font-size:1rem;color:#000;padding:1rem .8rem;min-width:300px;margin-top:3.5rem}.c-welcome__input::-webkit-input-placeholder{color:#000}.c-welcome__input:-ms-input-placeholder{color:#000}.c-welcome__input::placeholder{color:#000}@media (min-width: 768px){.c-welcome__input{margin-top:0}}

.c-button{color:#0098d5;font-size:.9rem;border:1px solid #0098d5;background:#fff;cursor:pointer;border-radius:.3rem;padding:.5rem 1.5rem;display:block;margin:1rem auto auto}.c-button:hover{background:#e9e9e9}

.c-game__cloud{border:1px solid #000;border-radius:.3rem;max-height:200px;max-width:300px;width:100%;height:100%;margin:auto auto 2rem}@media (min-width: 768px){.c-game__cloud{max-height:300px;max-width:500px}}.c-game__question{font-size:1.2rem}.c-game__question::first-letter{text-transform:capitalize}.c-game__word--selected{fill:#a5a5a5}

.c-score__title{margin-bottom:0}.c-score__result{font-size:2.2rem;margin:0}.c-score__points{color:#0098d5;display:block;margin-top:1rem}

